/*

 */

import React from 'react'
import {Result, List, WhiteSpace, Button, Modal,InputItem,ImagePicker,Radio} from 'antd-mobile'
import {connect} from 'react-redux'
import {Redirect} from 'react-router-dom'
import Cookies from 'js-cookie'
import {nanoid} from 'nanoid'
import {resetUser,updateUser} from '../../redux/actions'
import './Personal.css'

import {dealImage} from '../../utils/index'

const Item = List.Item

class Personal extends React.Component {

  state = {
      files: [],
      isEdit: false,  //
      fileURL: '',    //base64url
      fileName: '',
      newNickname: '',
      newFaith: '',
      newIntro: '',
      newTag: [],
      newSex:null,
      avatar: ''
  }

  //
  logout = () => {
    Modal.alert('', '?', [
      {text: ''},
      {
        text: '',
        onPress: ()=> {
          Cookies.remove('userid')
          this.props.resetUser()
          window.location.reload() //
        }
      }
    ])
  }

  //
  editInfo = () => {
      const {nickname, faith, tag, intro, sex, avatar} = this.props.user
      this.setState({
          newNickname: nickname,
          newFaith: faith,
          newIntro: intro,
          newTag: tag,
          newSex: sex,
          avatar: avatar
      })
      this.setState({isEdit: true})
  }

  //
  cancelEdit = () => {
      this.setState({isEdit: false})
  }

  //
  confirmUpdate = () => {
      Modal.alert('', '?', [
          {text: ''},
          {
              text: '',
              onPress: () => {
                  //files
                  this.setState({files: []}, () => {
                    this.props.updateUser(this.state)
                  })
                  this.setState({isEdit: false})
              }
          }
      ])
  }

  // base64URL  
  onAvatarChange = (files, type, index) => {
      if(files.length === 0) {
        this.setState({
          files,
          fileURL: '',
          fileName: ''
        });
      } else {
        dealImage(files[0].url, 150, (newBase64) => {
          this.setState({
            files,
            fileURL: newBase64,
            fileName: files[0].file.name
          });
        })
      }
  }
  //
  handleChange = (name, val) => {
      this.setState({
          [name]:val
      })
  }
  //
  handleTagChange = (val) => {
      this.setState({
        newTag: val.trim().split(/\s+/)
      })
  }

  handleNewSex = (num) => {
    this.setState({newSex: num})
  }

  render() {
    const {username, nickname, avatar, faith, tag, intro, sex, redirectTo} = this.props.user
    const {files, isEdit,newSex, newFaith, newIntro, newTag, newNickname} = this.state;
    // cookieuserid
    const userid = Cookies.get('userid')
    // , 
    if(!userid) {
      return <Redirect to='/login'/>
    }
    if(redirectTo === '/login') {
      return <Redirect to = {redirectTo}/>
    }
    return (
        <div>
            {isEdit ? 
                (
                    <div className="container" style={{marginBottom:50, marginTop:50}}>
                        <ImagePicker
                            className="user-avatar"
                            files={files}
                            length = {1}
                            onChange={this.onAvatarChange}
                            selectable={files.length < 1}
                            accept="image/gif,image/jpeg,image/jpg,image/png"
                        />
                        <p></p>
                        <List>
                          <InputItem defaultValue={newNickname}
                            onChange={(val) => this.handleChange('newNickname', val)}
                          > </InputItem>

                          <Item>
                             &nbsp;&nbsp;&nbsp;
                            &nbsp;
                            <Radio 
                              checked={newSex === 0}
                              className="my-radio"
                              onClick={() => this.handleNewSex(0)}
                            ></Radio>
                            &nbsp;&nbsp;
                            &nbsp;
                            <Radio 
                              checked={newSex === 1}
                              className="my-radio"
                              onClick={() => this.handleNewSex(1)}
                            ></Radio>
                            &nbsp;&nbsp;
                            &nbsp;
                            <Radio 
                              checked={newSex === 2}
                              className="my-radio"
                              onClick={() => this.handleNewSex(2)}
                            ></Radio>
                          </Item>

                          <InputItem style={{fontSize: 'smaller'}} 
                            defaultValue={newFaith}
                            onChange={(val) => this.handleChange('newFaith', val)}
                          > </InputItem>
                          
                          <InputItem style={{fontSize: 'smaller'}} 
                            defaultValue={newIntro}
                            onChange={(val) => this.handleChange('newIntro', val)}
                          > </InputItem>
                        
                          <InputItem 
                            style={{fontSize: 'smaller'}}
                            defaultValue={newTag.toString().replace(/,/g, ' ')}
                            onChange = {(val) => this.handleTagChange(val)}
                          > </InputItem>
                        </List>

                        <Button type='primary' onClick={this.confirmUpdate}></Button>
                        <Button type='warning' onClick={this.cancelEdit}></Button>
                    </div>
                )
                : 
                (
                    <div style={{marginBottom:50, marginTop:50}}>
                        <Result
                        img={<img src={avatar} style={{width: 60 , maxHeight: 65}} alt="header"/>}
                        title={nickname}
                        message={'' + username}
                        />
                        <List renderHeader={() => ''}>
                        <Item multipleLine>
                            <Item>{sex === 0 ? '' : sex===1 ? '' : ''}</Item>
                            <Item wrap>{faith}</Item>
                            <WhiteSpace/>
                            <Item wrap>: {intro}</Item>
                            <WhiteSpace/>
                            <Item wrap>: {tag.map( (item) => (<span key={nanoid()} className="user-tag"> #{item}# </span>))}</Item>
                        </Item>
                        </List>
                        <WhiteSpace/>
                        <List>
                            <WhiteSpace/>
                            <Button type='primary' onClick={this.editInfo}></Button>
                            <WhiteSpace/>
                            <Button type='warning' onClick={this.logout}></Button>
                            <WhiteSpace/>
                        </List>
                    </div>
                ) 
            }
        </div>

        
    )
  }
}

export default connect(
  state => ({user: state.user}),
  {resetUser,updateUser}
)(Personal)