/*

 */

import React, {Component} from 'react'
import {NavBar, InputItem, Icon, Button} from 'antd-mobile'
import {connect} from 'react-redux'
import {sendMsg, readMsg} from '../../redux/actions'
import './Chat.css'

class Chat extends Component {

    state = {
      content: '',
    }

    componentDidMount() {
      // 
      window.scrollTo(0, document.body.scrollHeight)
    }

    componentDidUpdate () {
        // 
        window.scrollTo(0, document.body.scrollHeight)
    }

    // 
    componentWillUnmount () {
        const from = this.props.match.params.userid
        const to = this.props.user._id
        this.props.readMsg(from, to)
    }

    //
    handleSend = () => {
        // 
        const from = this.props.user._id
        const to = this.props.match.params.userid
        const content = this.state.content.trim()
        // 
        if(content) {
          this.props.sendMsg({from, to, content})
        }
        // 
        this.setState({
          content: '',
        })
    }

    //
    contentOnChange = (val) => {
      this.setState({content: val})
    }

    render() {
        const {user} = this.props
        const {users, chatMsgs} = this.props.chat

        // chatId
        const meId = user._id
        // , 
        if(!users[meId]) { 
          return null
        }
        const targetId = this.props.match.params.userid
        const chatId = [meId, targetId].sort().join('_')

        // chatMsgs
        const msgs = chatMsgs.filter(msg => msg.chat_id===chatId)

        // 
        const targetAvatar = users[targetId].avatar;

        return (
            <div>
                <NavBar
                  icon={<Icon type='left'/>}
                  className='sticky-header'
                  onLeftClick={()=> this.props.history.goBack()}
                >
                    {users[targetId].nickname}
                </NavBar>
                <div style={{marginTop:50, marginBottom: 50}}>
                    {
                        msgs.map(msg => {
                          // 
                            if(targetId===msg.from) {
                                return (
                                    <div className='talk-box' key={msg._id} >
                                        <img src={targetAvatar} alt=""/>
                                        <span className="arrow-left"></span>
                                        <span className="bubble-left">{msg.content}</span>
                                    </div>
                                )
                          // 
                            } else { 
                                return (
                                    <div className='talk-box' key={msg._id} >
                                        <img src={user.avatar} className="right-avatar-img" alt=""/>
                                        <span className="arrow-right"></span>
                                        <span className="bubble-right">{msg.content}</span>
                                    </div>
                                )
                            }
                        })
                    }
                </div>

                {/*  +  */}
                <div className='am-tab-bar'>
                    <InputItem
                      placeholder=""
                      value={this.state.content}
                      onChange={(val) => this.contentOnChange(val)}
                      extra={
                          <Button inline size='small' onClick={this.handleSend}></Button>
                      }
                    />
                </div>
            </div>
        )
    }
}

export default connect(
  state => ({user: state.user, chat: state.chat}),
  {sendMsg, readMsg}
)(Chat)